<template>
  <div style="position: relative" id="drawArear">
    <div style="height: calc(100%); height: calc(100%)">
      <div
        style="
          height: 30px;
          width: 100%;
          position: absolute;
          left: 0;
          background-color: #ffffff;
          z-index: 9;
        "
      >
        <div
          style="
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 30px;
            white-space: nowrap;
          "
        >
          <span
            >&nbsp;
            {{
              selectedNum
                ? "当前选中设备 " + selectedNum
                : "当前筛选设备 " + this.activeResources.length
            }}
            | 共 {{ total }}&nbsp;&nbsp;</span
          >
        </div>
      </div>
      <!-- 主体 -->
      <div style="height: calc(100vh - 0px); width: 100%; overflow: auto">
        <div style="display: flex" v-loading="loading">
          <div
            style="margin-right: 40px"
            v-for="(item, index) in resourceGanteData"
            :key="item.resourceArea"
          >
            <div style="width: 100%; text-align: center">
              {{ item.resourceArea }}
            </div>
            <div style="display: flex; width: 2200px; flex-wrap: wrap">
              <div
                v-for="(itemData, _index) in item.resourceGanteVoList"
                :key="itemData.resourceZone + Math.random().toString()"
                style="flex-shrink: 0"
              >
                <Item
                  :ref="itemData.resourceZone"
                  :isGetHeigtData="isGetHeigtData"
                  :activeResources="activeResources"
                  @ctrlAllClick="ctrlAllClick"
                  v-model="resourceGanteData[index].resourceGanteVoList[_index]"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import _ from "lodash";
import moment from "moment";
import Item from "./item.vue";
import { initDrawReact } from "./select.js";
import { ganttData, heightData } from "./data.js";

export default {
  name: "StartupLayout",
  components: { Item },
  data() {
    return {
      total: 0,
      isGetHeigtData: false,
      showFullScreen: true,
      headerSearchInfo: {
        routingStepCode: "SL",
        resourceGroupCode: [],
        days: "7",
        startTime: new Date().toLocaleDateString(),
        productCode: [],
      },
      loading: false,
      resourceGanteData: [],
      operationProdList: [],
      groupCodes: [],
      activeResources: [],
    };
  },
  props: {},
  computed: {},
  watch: {
    resourceGanteData: {
      handler(val) {
        console.log("resourceGanteData");
        if (val.length) {
          this.$emit("newResourceData", val);
        }
      },
      deep: true,
    },
  },
  created() {
    this.refreshData();
  },
  mounted() {
    initDrawReact("drawArear", (ops) => {
      console.log(ops);
    });
  },
  computed: {
    selectedNum() {
      let num = 0;
      this.resourceGanteData.forEach((x) => {
        x.resourceGanteVoList?.forEach((item, index) => {
          item.resourceGanteList?.forEach((list) => {
            if (list.selected) {
              num++;
            }
          });
        });
      });

      return num;
    },
  },
  methods: {
    // 全屏
    fullScreen(num) {
      if (num == 1) {
        this.showFullScreen = false;
        let tableBox = document.querySelector("#top-1");
        tableBox.style.height = 0;
        // 获取gantt  id元素
        let ganttBox = document.querySelector("#bottom-1");
        ganttBox.style.height = "100%";
      } else {
        this.showFullScreen = true;
        let tableBox = document.querySelector("#top-1");
        tableBox.style.height = "50%";
        // 获取gantt  id元素
        let ganttBox = document.querySelector("#bottom-1");
        ganttBox.style.height = "50%";
      }
    },
    refreshData() {
      // this.loading = true;
      // setTimeout(() => {
      this.isGetHeigtData = false;
      this.resourceGanteData = this.setStatus(ganttData);
      let resourceRows = null;
      let count = 0;
      this.resourceGanteData.forEach((x) => {
        x.resourceGanteVoList?.forEach((item, index) => {
          if (resourceRows == item.resourceRows) {
            count++;
          } else if (resourceRows != item.resourceRows && resourceRows) {
            if (count > 0 && count < 10) {
              for (let i = 10; i > count; i--) {
                x.resourceGanteVoList.splice(index, 0, {
                  resourceZone: "none",
                });
              }
              count = 0;
            } else {
              count = 1;
            }
            resourceRows = item.resourceRows;
          } else {
            resourceRows = item.resourceRows;
            count = 1;
          }
        });
      });
      this.$nextTick(() => {
        // 定义变量用于存储鼠标按下的初始位置
        let startX, startY;
        // 监听鼠标按下事件，实现框选效果
        document.addEventListener("mousedown", (event) => {
          this.divs = document.querySelectorAll(".itemBox .item");
          // 记录鼠标按下时的初始位置
          startX = event.clientX;
          startY = event.clientY;
        });

        // 监听鼠标松开事件，结束框选
        document.addEventListener("mouseup", (event) => {
          // 获取鼠标松开时的位置
          let endX = event.clientX;
          let endY = event.clientY;
          // 遍历所有的div元素，判断是否被框选到
          for (let i = 0; i < this.divs.length; i++) {
            let div = this.divs[i];
            let rect = div.getBoundingClientRect();
            // 判断div是否在框选范围内
            if (
              rect.left >= Math.min(startX, endX) &&
              rect.right <= Math.max(startX, endX) &&
              rect.top >= Math.min(startY, endY) &&
              rect.bottom <= Math.max(startY, endY)
            ) {
              // 框选的div增加样式
              this.resourceGanteData.forEach((x) => {
                x.resourceGanteVoList.forEach((y) => {
                  if (
                    y.resourceGanteList?.some((z) => z.resourceCode === div.id)
                  ) {
                    this.$refs[y.resourceZone][0].value.resourceGanteList.find(
                      (z) => z.resourceCode === div.id
                    ).selected = true;
                  }
                });
              });
            }
          }
        });
      });
      // }, 2000);
    },
    setStatus(data) {
      this.total = 0;
      data.forEach((x) => {
        x.resourceGanteVoList.forEach((item) => {
          this.total += item.resourceGanteList.length;
          // 每个小区判断选中状态
          item.selected = false;
          // 每个小方块也能选中，给个状态，判断选中状态
          item.resourceGanteList.forEach((list) => {
            list.selected = false;
          });
        });
      });
      return data;
    },
    ctrlAllClick(data, value) {
      const { resourceZone } = data;
      const index = this.selectData.findIndex(
        (item) => item.resourceZone == resourceZone
      );
      if (value) {
        // 选择
        if (index > -1) {
          this.selectData.splice(index, 1, 0);
        }

        this.selectData.push(data);
      } else {
        // 取消选择
        index > -1 && this.selectData.splice(index, 1, 0);
      }
    },
  },
};
</script>
<style lang="scss"></style>
