<template>
  <div>
    <div :id="ganttData.resourceZone" class="itemBox" v-if="ganttData.resourceZone !== 'none'">
      <div class="itemTop" id="itemTop" :class="{ inactive: showResourceMain }">
        {{ ganttData.resourceZone }}
      </div>
      <div class="itemBottom">
        <div class="itemLeft">
          <div
            :id="getItem(item).resourceCode"
            class="item"
            :style="{
              backgroundColor: getItem(item).resourceColour ? getItem(item).resourceColour : '',
              opacity: isGetHeigtData ? (showResource(getItem(item).resourceCode) ? '1' : '0.4') : '0.8'
            }"
            v-for="item in itemLeft"
            :key="item"
          >
            {{ getItem(item).resourceCode }}
            <i v-show="getItemStatus(item)" class="el-icon-success icon" style="float: right;"></i>
          </div>
        </div>
        <div class="itemIntermediate" id="itemIntermediate">
          {{ ganttData.resourceMark }}
        </div>
        <div class="itemRight">
          <div
            :id="getItem(item).resourceCode"
            class="item"
            :class="{ inactive: showResource(getItem(item).resourceCode) }"
            :style="{
              backgroundColor: getItem(item).resourceColour ? getItem(item).resourceColour : '',
              opacity: isGetHeigtData ? (showResource(getItem(item).resourceCode) ? '1' : '0.4') : '0.8'
            }"
            v-for="item in itemRight"
            :key="item"
          >
            {{ getItem(item).resourceCode }}
            <i v-show="getItemStatus(item)" class="el-icon-success icon"></i>
          </div>
        </div>
      </div>
    </div>
    <div class="itemBoxNone" v-if="ganttData.resourceZone == 'none'"></div>
  </div>
</template>

<script>
import _ from 'lodash';

export default {
  name: 'StartupLayout',
  components: {},
  data() {
    return { selected: false, itemRight: [], itemLeft: [], mouseenterIsCtrl: false };
  },
  props: {
    value: {
      type: Object,
      default: () => {}
    },
    activeResources: {
      type: Array,
      default: () => []
    },
    isGetHeigtData: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    ganttData: {
      get() {
        return this.value;
      },
      set(newVal) {
        this.$emit('input', newVal);
      }
    },
    showResourceMain() {
      const arr =
        this.ganttData.resourceGanteList.filter(v => {
          return this.activeResources.indexOf(v.resourceCode) > -1;
        }) || [];

      if (arr.length > 0) {
        return false;
      } else {
        return true;
      }
    }
  },
  watch: {},
  created() {},
  mounted() {
    for (let index = 1; index <= 18; index++) {
      this.itemLeft.push(index);
    }

    for (let index = 19; index <= 36; index++) {
      this.itemRight.push(index);
    }

    // document.onmouseup = event => {
    //   this.removeAllEventListener();
    // };

    // 所有的小区添加全选事件
    const dom = document.getElementById(this.ganttData.resourceZone);
    dom &&
      dom.addEventListener('click', event => {
        event.stopPropagation();
        if (!(event.target.id == 'itemIntermediate' || event.target.id == 'itemTop')) return;
        if (event.ctrlKey) {
          // 小区全选
          this.ganttData.selected = !this.ganttData.selected;

          if (this.ganttData.selected) {
            this.ganttData.resourceGanteList.forEach(item => {
              item.selected = true;
            });
          } else {
            this.ganttData.resourceGanteList.forEach(item => {
              item.selected = false;
            });
          }
        }
      });

    // 所有的单元格添加点击事件
    this.$nextTick(() => {
      this.ganttData.resourceGanteList &&
        this.ganttData.resourceGanteList.forEach(item => {
          const dom = document.getElementById(`${item.resourceCode}`);
          dom &&
            dom.addEventListener('click', event => {
              event.stopPropagation();
              const resourceCode = item.resourceCode;
              if (event.ctrlKey || this.mouseenterIsCtrl) {
                const index = this.ganttData.resourceGanteList.findIndex(list => list.resourceCode == resourceCode);
                if (index > -1) {
                  this.ganttData.resourceGanteList[index].selected = !this.ganttData.resourceGanteList[index].selected;

                  if (!this.ganttData.resourceGanteList[index].selected) {
                    // 只要有一个单元格没选，那就是非全选状态
                    this.ganttData.selected = false;
                  } else {
                    // 单元格选满之后就是全选效果
                    if (
                      this.ganttData.resourceGanteList.filter(item => item.selected).length ==
                      this.ganttData.resourceGanteList.length
                    )
                      this.ganttData.selected = true;
                  }
                }
              }
            });

          // ctrl+鼠标左键，批量选择
          // dom.addEventListener('mouseenter', event => {
          //   event.stopPropagation();
          //   if (event.ctrlKey) {
          //     this.mouseenterIsCtrl = true;
          //     this.$nextTick(() => {
          //       dom.click();
          //     });
          //   } else {
          //     this.mouseenterIsCtrl = false;
          //   }
          // });
        });
    });
  },
  methods: {
    getItem(resourceLine) {
      const showData = this.ganttData.resourceGanteList.find(item => item.resourceLine == resourceLine);

      if (showData) {
        return showData;
      } else {
        return '';
      }
    },
    getItemStatus(resourceLine) {
      const showData = this.ganttData.resourceGanteList.find(item => item.resourceLine == resourceLine);

      if (showData) {
        return showData.selected;
      } else {
        return false;
      }
    },
    removeAllEventListener() {
      this.ganttData?.resourceGanteList?.forEach(item => {
        const dom = document.getElementById(`${item.resourceCode}`);
        dom.mouseenter = null;

        // console.log('remove:', item.resourceCode);
      });
    },
    showResource(resourceCode) {
      const index = this.activeResources.findIndex(item => item == resourceCode);

      if (index > -1) {
        return true;
      } else {
        return false;
      }
    }
  }
};
</script>
<style lang="scss" scoped>
$borderColor: rgba(0, 0, 0, 0.33);
.itemBox {
  height: 350px;
  width: 200px;
  border: 1px solid $borderColor;
  border-bottom: 0px solid $borderColor;
  margin-right: 20px;
  margin-top: 10px;
  cursor: pointer;
}
.itemBoxNone {
  height: 350px;
  width: 200px;
  margin-right: 20px;
  margin-top: 10px;
}
.inactive {
  background-color: #e0dfdf;
}
.itemTop {
  height: 20px;
  text-align: center;
  user-select: none;
  border-bottom: 1px solid $borderColor;
}

.itemBottom {
  height: calc(100% - 20px);
  display: flex;
}

.itemLeft {
  width: 40%;
  height: 100%;
  user-select: none;
}

.itemIntermediate {
  width: 20%;
  height: 100%;
  writing-mode: vertical-rl;
  text-align: center;
  line-height: 40px;
  border-left: 1px solid $borderColor;
  border-right: 1px solid $borderColor;
  border-bottom: 1px solid $borderColor;
  user-select: none;
}

.itemRight {
  width: 40%;
  height: 100%;
  user-select: none;
}

.item {
  height: calc(100% / 18);
  // position: relative;
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid $borderColor;
}

.saturate {
  filter: saturate(300%);
}
</style>
